/* STEPPER  */
.card-casting{
    width: 95%;
    margin-bottom: 3%;
}

.back-button-styles-css, .next-button-styles-css {
    margin: 12 auto !important;
    float: left;
    fill: white !important;
    background-color: #708090 !important;
    padding: 5px;
    border-radius: 50%;
}

.next-button-styles-css{
    float: right;
}

.back-button-styles-css:hover, .next-button-styles-css:hover{
    background-color: #22A7E0 !important;
}

.steps{
    background-color: transparent !important;
    color: red !important;
}


/* HEADERS */
th{
    color: #22A7E0;    
    padding:0;
    padding-bottom: 10px;
    border: none;
    font-weight: 500;
    font-size: 13px;
}

/* .centerText{
    text-align: center;
} */

.check, .checkOne {
    display: inline-block;
}

.checkOne {
    padding-left: 30%;
}

.rankDancer{
    -ms-flex-align: center;
        align-items: center;
}

.rank {
    padding: 10px;
    text-align: center;
}


/* AVAILABILITY CALENDAR */
.overlapAvailability{
    width: 60%;
    background-color: lightgray;
    height: 100%;
    position: absolute;
    display: inline-block;
    padding: 20px;
}


td{
    padding: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    border: none;
}

/* REMOVE HORIZONTAL LINES IN TABLE */
tr{
    border: none;
}

/* LAYOUT */
/* small card with cast filter */
.castList{
    width: 100%;
    position: relative;
}

/* HALF CARDS */
/*inside of small card with cast filter*/
.dancersList-filter{
    padding: 25px;

}


.dancersList-filter, .extraClass {
    height: 100%;
    width: 37%;
    position: relative;
    display: inline-block;
    margin-right: 3%;
  }

.castList-v2{
    width: 48%;
}

.cardAddMargin{
    margin-right: 4%;
}

/* RESOLVE CONFLICTS */
.conflictsCard{
    width: 100%;
    z-index: 1;
}

.conflictMessage{
    font-weight: 500;
    font-size: 15px;
    color: #e65100;
    margin: 0;
    padding-top: 10px;
    padding-bottom: 30px;
}

.smallHeading{
    font-size: 15px;
    font-weight: 500;
    padding-bottom: 20px;
}

.choreographersSelecteCast, .restOfCast{
    padding: 25px;
}


/* ADD DANCERS */

/* STEP 4 */
.setTimes{
    width: 100%;
    padding: 25px;
}

.chooseRehearsalTimes{
    position: relative;
    display: block;
    border: 1px solid lightgray;
    background-color: #fff;
    overflow: hidden;
    margin-right: 20px;
    margin-bottom: 10px;
    padding-left: 5px;
    border-radius: 3px;
    width: 100%;
}

.singleRehearsal{
    padding: 0;
    position: relative;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
}

.selectField{
    position: relative;
    display: inline-block;
    width: 100%;

}

.buttonsWrap{
    margin-top: 20px;
    height: 100%;
    position: relative;
    display: -ms-flexbox;
    display: flex;
}

.postCastingWrap{
    margin-top: 10%;
}

.importantText{
    color: #22A7E0;
}

.warningText{
    font-size: 15px;
}

.choreos{
    display: inline-block;

}

.dancerAssignedNumber{
    font-weight: 600;
    padding-right: 5px;
}

.personRankBoxes{
    text-align: center;
}


/**Hover over exclamation point**/

.tooltip {
    position: relative;
    display: inline-block;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -60px;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}


